<template>
  <div>
    <template v-if="showAlertSelected">
      <Alert v-if="status.is_usps_return_actived && successActivation" color="green" view="smooth" dense>
        <div class="flex items-center gap-x-2">
          <span class="material-icons text-4xl">check</span>
          Your USPS Return Account has been successfully activated.
        </div>
        <template #right>
          <span
            class="material-icons cursor-pointer"
            @click="closeAlert">
            close
          </span>
        </template>
      </Alert>
      <div v-if="!status.is_usps_return_actived && !status.is_usps_return_processing" class="mb-4 bg-blue-01 bg-opacity-50 px-6 py-4 rounded-md flex gap-x-4 text-blue-03">
        <span class="material-icons text-4xl">info</span>
        <div class="h-full">
          <p class="mb-4">
            Please reach out to EasyPost at support@easypost.com to get approval for a Pay On Delivery USPS account (POD) before integration. If your POD USPS account is already approved by EasyPost, you can proceed"
          </p>
          <Button
            color="blue"
            :to="{ name: 'integrationEasypostActivation' }"
            link>
            Activate USPS Return Account
          </Button>
        </div>
      </div>
      <div v-else-if="!status.is_usps_return_actived && status.is_usps_return_processing" class="mb-4 bg-orange-01 bg-opacity-50 px-6 py-4 rounded-md flex gap-x-4 text-orange-03">
        <span class="material-icons text-4xl">info</span>
        <div class="h-full">
          <p class="mb-4">
            Your USPS Return Account has been activated. Please contact Easypost to enable the service for your account
          </p>
        </div>
      </div>
    </template>
    <p class="font-semibold my-6">
      Choose a shipping courier for your trade-ins
    </p>
    <div>
      <ShippingProvider
        @selected="handleSelectedCarrier"
        :carriers="carriers"
        :selected="selectedCarrier"
        :filters="filters"
      ></ShippingProvider>
    </div>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue'
import { useShippingProvider } from 'composable/integration/easypost'

import ShippingProvider from 'components/Integration/ShippingProvider.vue'
import Button from 'bbUi/components/button/button.vue'
import Alert from 'bbUi/components/alert/alert.vue'

export default defineComponent({
  components: {
    Alert,
    Button,
    ShippingProvider
  },
  async setup() {
    const successActivation = ref(true)
    const { carriers, filters, status, selectedCarrier, selectCarrier } =
      useShippingProvider()
    const handleSelectedCarrier = (selectedCarrier) => {
      selectCarrier(selectedCarrier)
    }
    const showAlertSelected = computed(() => {
      if (selectedCarrier.value && Object.keys(selectedCarrier.value).length && selectedCarrier.value.carrier_name && selectedCarrier.value.carrier_name.includes('USPS')) return true

      return false
    })
    const closeAlert = () => successActivation.value = false

    return {
      carriers,
      filters,
      status,
      selectedCarrier,
      closeAlert,
      successActivation,
      showAlertSelected,
      handleSelectedCarrier
    }
  }
})
</script>
